<template>
  <div>
    <v-app-bar elevation="4" color="blue" dark fixed>
      <v-icon @click="$router.go(-1)">chevron_left</v-icon>
      <span style="margin-right: auto; margin-left: auto">{{
        title || listPageType
      }}</span>
    </v-app-bar>
    <div class="d-flex">
      <div
        v-if="showBtn"
        class="d-flex"
        style="margin-top: -10px; margin-right: 5px"
      >
        <v-icon>swap_vert</v-icon>
        <v-icon>filter_alt</v-icon>
      </div>
      <v-text-field
        v-if="showSearch"
        class="mb-3"
        placeholder="請輸入關鍵字"
        v-model="search"
        outlined
        hide-details
        rounded
        dense
        color="blue"
      >
        <template #append>
          <div>
            <v-icon>search</v-icon>
          </div>
        </template>
      </v-text-field>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  props: {
    showBtn: {
      type: Boolean,
      default: false,
    },
    showSearch: {
      type: Boolean,
      default: true,
    },
    title: {
      type: String,
      default: "",
    },
  },

  data: () => ({
    search: "",
  }),

  computed: {
    ...mapState(["listPageType"]),
  },
};
</script>

<style scoped>
.overflow {
  overflow: hidden !important;
}
</style>